import React, { useEffect, useRef, useState } from "react";
import { EditorContent, useEditor } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import Placeholder from "@tiptap/extension-placeholder";
import { Plugin, PluginKey } from "prosemirror-state";
import { Decoration, DecorationSet } from "prosemirror-view";

/* ---------- syllable + metric helpers ---------- */
function countSyllables(word) {
  const w = (word || "").toLowerCase().replace(/[^a-z]/g, "");
  if (!w) return 0;
  if (w.length <= 3) return 1;
  const cleaned = w.replace(/(?:es|ed|e)$/g, "");
  const m = cleaned.match(/[aeiouy]+/g);
  return Math.max(1, m ? m.length : 0);
}
function splitSentences(text) {
  return (text || "")
    .replace(/\s+/g, " ")
    .trim()
    .split(/(?<=[\.!?])\s+/)
    .filter(Boolean);
}

/* ---------- decoration plugin ---------- */
const decoKey = new PluginKey("sentenceHighlighter");

function mapOffsetToPos(doc, offset) {
  let seen = 0;
  let result = 0;
  doc.descendants((node, posHere) => {
    if (node.isText) {
      const len = node.text.length;
      if (offset <= seen + len) {
        result = posHere + (offset - seen);
        return false;
      }
      seen += len;
    } else if (node.isBlock) {
      // textBetween inserts a newline between blocks; count it
      if (seen < offset) seen += 1;
    }
    return undefined;
  });
  return result;
}

function buildDecorations(doc, cfg) {
  const { targetGrade, showHardWord, exemptWords, easyWords } = cfg;
  const text = doc.textBetween(0, doc.content.size, "\n", "\n");
  const decos = [];

  // ----- Sentence highlights -----
  let offset = 0;
  const sentences = splitSentences(text);
  for (const s of sentences) {
    const ws = s.trim().split(/\s+/).filter(Boolean);
    if (!ws.length) {
      offset += s.length + 1;
      continue;
    }
    let syll = 0;
    for (const w of ws) syll += countSyllables(w);
    const grade = 0.39 * (ws.length / 1) + 11.8 * (syll / ws.length) - 15.59;

    // Yellow rule (20 words OR  target+1)
    if (ws.length >= 20 || grade >= targetGrade + 1) {
      const from = mapOffsetToPos(doc, offset);
      const to = mapOffsetToPos(doc, offset + s.length);

      // Red rule (25 words OR  target+3)
      if (ws.length >= 25 || grade >= targetGrade + 3) {
        decos.push(
          Decoration.inline(from, to, {
            class: "hl-very",
            title:
              "This sentence is likely too long or complex. Shortening it will make it easier to read."
          })
        );
      } else {
        decos.push(
          Decoration.inline(from, to, {
            class: "hl-slight",
            title:
              "This sentence may be a bit hard to follow. Try shortening it or using simpler words."
          })
        );
      }
    }

    offset += s.length + 1;
  }

  // ----- Likely hard words -----
  if (showHardWord) {
    const wordRegex = /\b[\w']+\b/g;
    let m;
    while ((m = wordRegex.exec(text)) !== null) {
      const raw = m[0];
      const lower = raw.toLowerCase();

      // Exemptions
      if (exemptWords.has(lower)) continue; // proper nouns, phrases normalized to words list
      if (easyWords.has(lower)) continue;   // DaleChall easy list

      // Heuristic: 3+ syllables
      if (countSyllables(raw) >= 3) {
        const from = mapOffsetToPos(doc, m.index);
        const to = mapOffsetToPos(doc, m.index + raw.length);
        decos.push(
          Decoration.inline(from, to, {
            class: "hl-hardword",
            title: "This word may be hard for some readers. Consider a simpler alternative."
          })
        );
      }
    }
  }

  return DecorationSet.create(doc, decos);
}

function highlighterPlugin(cfgRef) {
  return new Plugin({
    key: decoKey,
    state: {
      init: (_, { doc }) => buildDecorations(doc, cfgRef.current),
      apply(tr, oldSet, _old, state) {
        const needsUpdate = tr.docChanged || tr.getMeta(decoKey) === "recompute";
        return needsUpdate
          ? buildDecorations(state.doc, cfgRef.current)
          : oldSet.map(tr.mapping, tr.doc);
      }
    },
    props: {
      decorations(state) {
        return this.getState(state);
      }
    }
  });
}

/* ---------- component ---------- */
export default function TiptapEditor({
  targetGrade,
  showHardWord,
  onReady,
  onPlainTextChange
}) {
  const [exemptWords, setExemptWords] = useState(new Set());
  const [easyWords, setEasyWords] = useState(new Set());

  const cfgRef = useRef({ targetGrade, showHardWord, exemptWords, easyWords });

  // Load proper-noun exemptions
  useEffect(() => {
    fetch("/exemptProperNouns.json")
      .then(res => res.json())
      .then(data => {
        const singles = (data.singleWords || []).map(w => (w || "").toLowerCase());
        const phrases = (data.phrases || [])
          .map(p => (p || "").toLowerCase().split(/\s+/))
          .flat(); // simple normalization so common phrase tokens are skipped
        setExemptWords(new Set([...singles, ...phrases]));
      })
      .catch(() => {});
  }, []);

  // Load DaleChall easy words
  useEffect(() => {
    fetch("/daleChallEasyWords.json")
      .then(res => res.json())
      .then(arr => setEasyWords(new Set((arr || []).map(w => (w || "").toLowerCase()))))
      .catch(() => {});
  }, []);

  // Keep config fresh + recompute decorations
  useEffect(() => {
    cfgRef.current = { targetGrade, showHardWord, exemptWords, easyWords };
  }, [targetGrade, showHardWord, exemptWords, easyWords]);

  // Create TipTap editor
  const editor = useEditor({
    extensions: [
      StarterKit,
      Placeholder.configure({
        placeholder:
          "Paste or type. Sentences that run long or read above your target will highlight here."
      }),
      highlighterPlugin(cfgRef)
    ],
    editorProps: {
      attributes: { class: "editor" }
    },
    onUpdate({ editor }) {
      const plain = editor.state.doc.textBetween(
        0,
        editor.state.doc.content.size,
        " ",
        " "
      );
      onPlainTextChange?.(plain);
      // trigger re-evaluation (safeguard)
      editor.view.dispatch(editor.state.tr.setMeta(decoKey, "recompute"));
    }
  });

  // Pass editor instance up when ready
  useEffect(() => {
    if (editor) onReady?.(editor);
  }, [editor, onReady]);

  return <EditorContent editor={editor} />;
}
